<template>
  <section id="main-section">
    <article style="z-index: 1;">
      <div class="row mb20">
        <div class="col title-wrap">
          <h1 ref="title_txt"></h1>
        </div>
      </div>
      <div class="box-shadow row" ref="box_article">
        <div class="col">
          <div class="profile-img" :style="`background-image: url(${profileImage})`">
          </div>
        </div>
        <div class="col flx column">
          <h4 class="mb6">"안되면 될 때 까지"</h4>
          <p class="title">맡은 일은 어려움에 직면하더라도 해결책을 찾아 끝내 완수하는 개발자입니다.</p>
          <br/>
          <p class="sub-title mb6">직관적인 소스 작성을 선호합니다.</p>
          <p class="sub-title mb6">컴포넌트의 재사용성을 고려하여 개발합니다.</p>
          <p class="sub-title mb6">새로운 웹 기술과 지식에 대해 관심이 많습니다.</p>
          <p class="sub-title mb6">능동적인 커뮤니케이션 및 협업으로 문제 해결과 비즈니스 성장에 적극적인 자세로 임하고 있습니다.</p>
          <p class="sub-title">더 나은 서비스를 만드는 것을 함께 고민하고, 부족함을 서로 채울 수 있는 동료가 되고 싶습니다.</p>
        </div>
      </div>
    </article>
    <article class="bg" ref="tag_article">
    </article>
  </section>
</template>

<script>
import { ref, onMounted } from 'vue';
import profileImage from '@/assets/images/profilePicture.jpg';

export default {
  setup() {
    // **** box article animation ****
    const box_article = ref();

    //  **** title animiation **** 
    const title_txt = ref();
    let content;
    let txtIdx = 0;
    
    const typing = () => {
      if(document.body.offsetWidth > 560) content = '안녕하세요 :)\n프론트엔드 개발자, 김수은입니다.';
      else content = '안녕하세요 :)\n프론트엔드 개발자,\n 김수은입니다.';

      
      let txt = content[txtIdx++];
      if (txt === undefined) return;
      if (!title_txt.value) return; // 함수실행중 페이지 이동 오류 방지
      title_txt.value.innerHTML += txt === '\n'? '<br/>' : txt;

      if (txtIdx === content.length) {
        // all article opacity 1
        box_article.value.style.opacity = 1;
        tag_article.value.style.opacity = 1;
      }
      if (txtIdx > content.length) txtIdx = 0;
      else setTimeout(typing, 80);
    };

    // 초기화 + 타이핑 실행
    const getTitle = () => {
      txtIdx = 0;
      if(title_txt.value) title_txt.value.innerHTML = '';
      typing();
    }

    // **** 움직이는 Tag Box **** 
    const tag_article = ref();
    const TAGS = ['HTML', 'CSS', 'JavaScript', 'Typescript', 'Tailwind', 'Vue2', 'Vue3', 'Nuxt.js', 'SASS', 'RESTful', 'graphQL', 'UI/UX', 'SVG', 'animation', 'SCSS', 'PHP', 'VueX', 'node.js', 'jQuery', 'Vite', 'pinia', 'ResponsiveWeb', 'Accessibility', 'CrossBrowsing', 'WebService', 'BootStrap', 'Communication', 'HTTP', 'Internet', 'ES6', 'ESLint', 'Prettier', 'Express.js', 'GIT'];

    // random
    const random = (min, max) => Math.floor(Math.random() * (max - min)) + min;
    const shuffle = (arr) => [...arr].sort( () => .5 - Math.random() );

    const loadData = () => {
      // tag List 추가
      const tagList = document.createElement('div');
      tagList.classList.add('tag-list');

      [...new Array(5)].map((item, idx) => {
        let duration = random(10000, 15000);
        let reverse= idx % 2;
        let slider = document.createElement('div');
        slider.classList.add('loop-slider');
        slider.style.setProperty('--duration', `${duration}ms`);
        slider.style.setProperty('--direction', `${reverse? 'reverse' : 'normal'}`);
        
        // inner 추가
        let inner = document.createElement('div');
        inner.classList.add('inner');
        shuffle(TAGS).map(tag => {
          // tag추가
          let tagItem = document.createElement('div');
          tagItem.classList.add('tag');
          tagItem.innerHTML= `<span>#</span>${tag}`;
          inner.appendChild(tagItem);
        });
        slider.appendChild(inner);

        // slider 추가
        tagList.appendChild(slider);
      });

      // fade 추가
      let fade = document.createElement('div');
      fade.classList.add('fade');
      tagList.appendChild(fade);

      tag_article.value.appendChild(tagList);
    }

    onMounted(() => {
      loadData();
      // title animation 실행
      typing();
    });

    window.addEventListener("resize", getTitle);

    return {
      title_txt,
      box_article,
      tag_article,
      profileImage
    };
  }
}
</script>